import React, { Component } from 'react';
import r1 from './r.jpg'
import p1 from './p.jpg'
import s1 from './s.jpg'
class RPS extends React.Component {
    state={
        Result:"None",
        User:"None",
        PC:"None",
        score:0
    }
    fun1=(e)=>
    {
        let a =e.target.name;
        let b= Math.round(Math.random()*2);
        if(b==0)
            b="rock"
        else if(b==1)  
            b="paper"
        else if(b==2)
            b="scissor"
       
        this.setState({User:a})
        this.setState({PC:b})
        if(a=="rock" && b=="rock")
        {
            this.setState({Result:"Match Tied"})
        }
        else if(a=="rock" && b=="paper")
        {
            this.setState({Result:"Computer Win"})
        }
        else if(a=="rock" && b=="scissor")
        {
            this.setState({Result:"User Win"})
            this.setState({score:1+this.state.score})
        }
        else if(a=="paper" && b=="rock")
        {
            this.setState({Result:"User Win."})
            this.setState({score:1+this.state.score})
        }
        else if(a=="paper" && b=="paper")
        {
            this.setState({Result:"Match Tied"})
        }
        else if(a=="paper" && b=="scissor")
        {
            this.setState({Result:"Computer Win"})
        }


        else if(a=="scissor" && b=="rock")
        {
            this.setState({Result:"Computer Win."})
        }
        else if(a=="scissor" && b=="paper")
        {
            this.setState({Result:"user Win"})
            this.setState({score:1+this.state.score})
        }
        else if(a=="scissor" && b=="scissor")
        {
            this.setState({Result:"Match Tied"})
        }
    }
    render() { 
        return <>
        <div>
            <div>
                
                <h1 style={{textAlign:"center"}}>ROCK PAPER SCISSOR</h1>
            </div>
            <div style={{background:"#CCE1E3", justifyContent:"center",display:"flex"}}>
              <img src={r1} style={{border:"1px solid", borderRadius:"100px" ,margin:"10px" ,cursor:"pointer"}} name="rock" onClick={this.fun1}/>  
              <img src={p1} style={{border:"1px solid", borderRadius:"100px" ,margin:"10px" ,cursor:"pointer"}} name="paper" onClick={this.fun1}/>
              <img src={s1} style={{border:"1px solid", borderRadius:"100px" ,margin:"10px" ,cursor:"pointer"}}name="scissor" onClick={this.fun1}/>
            </div>
            <div style={{background:"#CCE1E3", display:"flex" ,justifyContent:"center",flexDirection:"column",alignItems:"center"}}>
            <h1 style={{color:"green"}}>User:{this.state.User}</h1>
            <h1 style={{color:"red"}}>Computer:{this.state.PC}</h1>
            <h1 style={{color:"black"}}>Result:{this.state.Result}</h1>
            <h1 style={{color:"gray"}}>Points:{this.state.score}</h1>
            </div>
            <div> <h3 style={{color:"gray", float:"left"}}> &copy; Hasnain Raza  </h3> 
            &nbsp;<a href="https://github.com/Hasnain-raza12" style={{color:"blue" ,float:"left"}}> <h4>  Click Here </h4></a>
            </div>
        </div>;
        </>
    }
}
 
export default RPS;